import {
  RecipeHeroConteiner,
  HeroTitle,
  CookingTime,
  HeroText,
} from './RecipePageHero.styled';
import { ButtonSkew } from 'components/ButtonSkew/ButtonSkew';
import { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { getFavorite } from 'redux/ownRecipes/ownRecipesOperations';
import { getFavoriteRecipes } from 'redux/ownRecipes/ownRecipesSelectors';
import sprite from '../../images/sprite.svg';
import {
  addFavorite,
  deleteFavorite,
} from 'redux/ownRecipes/ownRecipesOperations';
import { MotivatedModal } from 'components/MotivatedModal/MotivatedModal';

const RecipePageHero = ({ meal, idMeal, about, cookingTime }) => {
  const [btnText, setBtnText] = useState(false);
  const [triger, setTriger] = useState(0);

  const dispatcher = useDispatch();
  const obj = useSelector(getFavoriteRecipes);

  function deleteFromFav() {
    dispatcher(deleteFavorite(idMeal));
    setBtnText(false);
    return;
  }

  // function toFavorite() {
  //   console.log(btnText);

  //   if (btnText) {
  //     dispatcher(deleteFavorite(idMeal));
  //     setBtnText(false);
  //     console.log(btnText);
  //     return;
  //   }

  //   dispatcher(addFavorite(idMeal));
  //   setBtnText(true);
  //   return;
  // }

  function addtoFavorite() {
    dispatcher(addFavorite(idMeal));
    setBtnText(true);
    obj.length === 0 ? setTriger(1) : setTriger(0);
    obj.length === 9 ? setTriger(10) : setTriger(0);
    return;
  }

  function getIngDescription(recipeName) {
    if (obj !== undefined) {
      const recipe = obj.some(recipe => recipe.strMeal === recipeName);
      return recipe;
    }
    return false;
  }

  useEffect(() => {
    dispatcher(getFavorite({}));
  }, [dispatcher]);

  return (
    <>
      {triger === 1 && <MotivatedModal type="first favorite" isOpen={true} />}
      {triger === 10 && <MotivatedModal type="ten-recipes" isOpen={true} />}
      <RecipeHeroConteiner>
        <HeroTitle>{meal}</HeroTitle>
        <HeroText>{about}</HeroText>
        {btnText || getIngDescription(meal) ? (
          <ButtonSkew
            type="button"
            text={'Remove from favorite recipes'}
            styled="other"
            location="recipes"
            fn={deleteFromFav}
          />
        ) : (
          <ButtonSkew
            type="button"
            text="Add to favorite recipes"
            styled="other"
            location="recipes"
            fn={addtoFavorite}
          />
        )}
        {cookingTime !== '' ? (
          <CookingTime>
            <svg>
              <use href={sprite + `#icon-clock`} />
            </svg>
            <span>{cookingTime + ` min`}</span>
          </CookingTime>
        ) : (
          <CookingTime></CookingTime>
        )}
      </RecipeHeroConteiner>
    </>
  );
};

export default RecipePageHero;
